import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import './style.scss';

const Header = props => (
  <div className="header">
    {props.location.pathname !== '/' && (
      <button
        to="/"
        onClick={e => {
          e.preventDefault();
          props.history.goBack();
        }}
        className="sidenav-button-back"
        style={{ display: props.location.pathname === '/' ? 'none' : 'block' }}
      >
        <i className="fas fa-arrow-left" />
      </button>
    )}
    {props.location.pathname === '/' && (
      <button onClick={props.toggleSidenav} className="side-nav-show">
        <i className="fas fa-bars" />
      </button>
    )}

    <h1 className="title">{props.title} </h1>
    {props.location.pathname !== "/" && (
      <Link to="/" className="sidenav-button-home">
        <i className="fas fa-home" />
      </Link>
    )}
  </div>
);

Header.propTypes = {
  history: PropTypes.object.isRequired, // eslint-disable-line
  location: PropTypes.object.isRequired,// eslint-disable-line
  title: PropTypes.string.isRequired, // eslint-disable-line
  toggleSidenav: PropTypes.func.isRequired
};

export default withRouter(Header);



