import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import './style.scss';

class Sidenav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true
    };
  }
  render() {
    return (
      <div
        className={`side-nav side-nav--animatable ${
          this.props.visible ? 'side-nav--visible' : ''
        }`}
      >
        <nav className="side-nav__container">
          <button onClick={this.props.toggleSidenav} className="side-nav__hide">
            <i className="far fa-times-circle" />
          </button>
          <header className="side-nav__header">
            <span className="side-nav-user">
              <i className="fas fa-user-circle" />
              {this.props.user || "Anonymous"}
            </span>
          </header>
          <ul className="side-nav__content">
            <li>
              <NavLink to="/" activeClassName="selected" exact>
                <i className="fab fa-fort-awesome" />Home
              </NavLink>
            </li>
            <li>
              <NavLink to="/delivery" activeClassName="selected" exact>
                <i className="fab fa-dropbox" />Delivery
              </NavLink>
            </li>
            <li>
              <NavLink to="/shipping" activeClassName="selected" exact>
                <i className="fas fa-truck" />
                Shipping
              </NavLink>
            </li>
            <li className="separator" />

            <li>
              <button onClick={this.props.logout}>
                <i className="fas fa-sign-out-alt" />
                Logout
              </button>
            </li>
          </ul>
        </nav>
      </div>
    );
  }
}

Sidenav.propTypes = {
  toggleSidenav: PropTypes.func.isRequired,
  visible: PropTypes.bool.isRequired
};

export default Sidenav;



