import React, { Component } from 'react';
import { Link, Route, Switch, withRouter } from 'react-router-dom';
import autoBind from 'auto-bind';
import Picking from './components/picking';
import Packing from './components/packing';
import Header from '../common/header';
import './style.scss';

class Delivery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pageTitle: 'Delivery'
    };
    this.pageTitle = 'Delivery';
    autoBind(this);
  }
  componentDidMount() {
    this.props.setHeader(this.pageTitle);
  }
  gotoPicking() {
    return <Picking setHeader={this.props.setHeader} />;
  }
  gotoPacking() {
    return <Packing setHeader={this.props.setHeader} />;
  }
  render() {
    return (
      <section className="section-wrapper delivery">
        <Header
          title={this.state.pageTitle}
          toggleSidenav={this.props.toggleSidenav}
        />
        <div className="section-content">
          <h2>Delivery List </h2>
          <p>
            It is a long established fact that a reader will be distracted by
            the readable content of a page when looking at its layout. The point
            of using Lorem Ipsum is that it has a more-or-less normal
            distribution of letters, as opposed to using 'Content here, content
            here', making it look like readable English.
          </p>
          <Link to="/delivery/picking" className="link">
            picking{' '}
          </Link>
          <p>
            Many desktop publishing packages and web page editors now use Lorem
            Ipsum as their default model text, and a search for 'lorem ipsum'
            will uncover many web sites still in their infancy. Various versions
            have evolved over the years, sometimes by accident, sometimes on
            purpose (injected humour and the like).
          </p>
          <Link to="/delivery/packing" className="link">
            Packing{' '}
          </Link>
          <Switch location={this.props.location}>
            <Route path="/delivery/picking" render={this.gotoPicking} />
            <Route path="/delivery/packing" render={this.gotoPacking} />
          </Switch>
        </div>
      </section>
    );
  }
}

export default withRouter(Delivery);



