import React, { Component } from 'react';
import { Route, Switch, withRouter } from 'react-router-dom';
// import { CSSTransition, TransitionGroup } from 'react-transition-group';
import PropTypes from 'prop-types';
import autoBind from 'auto-bind';
import Login from '../login';
import Dashboard from '../dashboard';
import PrivateRoute from '../common/privateRoute';
import Delivery from '../delivery';
import Loader from '../common/loader';
import Shipping from '../shipping';

class Root extends Component {
  constructor(props) {
    super(props);
    let user = 'Anonymous';
    let login = false;
    if (localStorage.user) {
      user = localStorage.user ? localStorage.user : 'Anonymous';
      login = true;
    }
    this.state = {
      login, // eslint-disable-line
      loginError: false,
      user,
      pageTitle: 'Dashboard',
      loading: false
    };
    autoBind(this);
  }
  setHeader(pageTitle) {
    this.setState({ pageTitle });
  }
  fakeLogin(user, password) {
    if (user.toLowerCase() === 'demo' && password.toLowerCase() === 'demo') {
      this.setState({ loading: true });
      setTimeout(() => {
        localStorage.user = user;
        this.setState({ login: true, user, loginError: false, loading: false });
      }, 1000);
    } else {
      this.setState({ loginError: true });
    }
  }

  realLogout() {
    this.setState({ loading: true });

    setTimeout(() => {
      this.setState({ login: false, user: 'Anonymous', loading: false });
      localStorage.removeItem('user');
    }, 1000);
  }
  gotoLogin() {
    return (
      <Login
        login={this.fakeLogin}
        isAuthenticated={this.state.login}
        error={this.state.loginError}
        resetLoginError={this.resetLoginError}
      />
    );
  }
  toggleSidenav() {
    this.setState({ sidenavVisibility: !this.state.sidenavVisibility });
  }
  resetLoginError() {
    this.setState({
      loginError: false
    });
  }
  render() {
    // if (!this.props.login && this.props.location.pathname !== '/login') {
    //   return <Redirect to="/login" />;
    // }
    // const currentKey = this.props.location.pathname;
    // const timeout = this.props.login === false ? {enter:0, exit:0 } : { enter: 250, exit: 175 };
    return (
      // <TransitionGroup component="main" className="page-main">
      //   <CSSTransition key={currentKey} timeout={timeout} classNames="fade">
      <section className="page-main-inner">
        {this.state.loading && <Loader />}
        <Switch location={this.props.location}>
          <PrivateRoute
            exact
            path="/"
            props={this.props}
            user={this.state.user}
            isAuthenticated={this.state.login}
            title={this.state.pageTitle}
            toggleSidenav={this.toggleSidenav}
            setHeader={this.setHeader}
            logout={this.realLogout}
            component={Dashboard}
          />
          <PrivateRoute
            path="/delivery"
            props={this.props}
            isAuthenticated={this.state.login}
            toggleSidenav={this.toggleSidenav}
            title={this.state.pageTitle}
            setHeader={this.setHeader}
            component={Delivery}
          />
          <PrivateRoute
            exact
            path="/shipping"
            props={this.props}
            isAuthenticated={this.state.login}
            toggleSidenav={this.toggleSidenav}
            title={this.state.pageTitle}
            setHeader={this.setHeader}
            component={Shipping}
          />
          <Route path="/login" render={this.gotoLogin} />
        </Switch>
      </section>
      //   </CSSTransition>
      // </TransitionGroup>
    );
  }
}

Root.propTypes = {
  location: PropTypes.object.isRequired // eslint-disable-line react/forbid-prop-types
};

const App = withRouter(Root);

export default App;



