import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import autoBind from 'auto-bind';

class Picking extends Component {
  constructor(props) {
    super(props);
    autoBind(this);
  }
  componentDidMount() {
    this.props.setHeader('Picking');
  }
  gotoDelivery() {
    this.props.history.replace('/delivery');
  }
  gotoPacking() {
    this.props.history.replace('/delivery/packing');
  }
  render() {
    return (
      <div className="sub-page packing">
        {/* <button onClick={this.gotoDelivery}>Delivery </button>
        <button onClick={this.gotoPacking}>Packing </button> */}
        <h2> Picking </h2>
        <p>
          It is a long established fact that a reader will be distracted by the
          readable content of a page when looking at its layout. The point of
          using Lorem Ipsum is that it has a more-or-less normal distribution of
          letters, as opposed to using 'Content here, content here', making it
          look like readable English.
        </p>
      </div>
    );
  }
}

export default withRouter(Picking);



