import React, { Fragment, Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import autoBind from 'auto-bind';
import './style.scss';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      userEmpty: false,
      password: '',
      passwordEmpty: false
    };
    autoBind(this);
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.isAuthenticated) {
      this.props.history.replace('/');
    }
  }
  onEnter(e) {
    if (e.keyCode === 13 && this.checkValidity())
      this.props.login(this.state.username, this.state.password);
  }
  checkValidity() {
    let valid = true;
    if (this.state.username === '') {
      valid = false;
      this.setState({ userEmpty: true });
    }
    if (this.state.password === '') {
      valid = false;
      this.setState({ passwordEmpty: true });
    }
    return valid;
  }
  updateUsername(e) {
    this.setState({
      username: e.target.value,
      userEmpty: false,
      passwordEmpty: false
    });
    if (this.props.error) {
      this.props.resetLoginError();
    }
  }
  updatePassword(e) {
    this.setState({
      password: e.target.value,
      passwordEmpty: false,
      userEmpty: false
    });
    if (this.props.error) {
      this.props.resetLoginError();
    }
  }
  login() {
    if (this.checkValidity())
      this.props.login(this.state.username, this.state.password);
  }
  resetForm() {
    this.setState({
      username: '',
      password: '',
      userEmpty: false,
      passwordEmpty: false
    });
    this.props.resetLoginError();
  }
  render() {
    return (
      <Fragment>
        <section className="section-wrapper login">
          <div className="login-title">
            <img src="/logo.png" alt="Logo" />
            <h1>Seed Handling</h1>
          </div>
          <div className="login-form">
            <div className="form-input">
              <input
                type="text"
                placeholder="username"
                value={this.state.username}
                onChange={this.updateUsername}
                onKeyDown={this.onEnter}
              />
              {this.state.userEmpty && (
                <p className="error"> Username is required. </p>
              )}
            </div>
            <div className="form-input">
              <input
                type="password"
                placeholder="password"
                value={this.state.password}
                onChange={this.updatePassword}
                onKeyDown={this.onEnter}
              />
              {this.state.passwordEmpty && (
                <p className="error"> Password is required. </p>
              )}
            </div>
            <div className="action-buttons">
              {this.props.error && (
                <p className="error">
                  Either username or password is incorrect !
                </p>
              )}
              <button className="login-form-submit" onClick={this.login}>
                login
              </button>
              <button className="login-form-reset" onClick={this.resetForm}>
                reset
              </button>
            </div>
          </div>
        </section>
      </Fragment>
    );
  }
}

Login.propTypes = {
  login: PropTypes.func.isRequired,
  resetLoginError: PropTypes.func.isRequired,
  error: PropTypes.bool.isRequired,
  isAuthenticated: PropTypes.bool.isRequired,
  history: PropTypes.object.isRequired  // eslint-disable-line

};

export default withRouter(Login);



